nav {
    background-color: var(--main-theme-color);
    display: flex;
    padding: 1.5rem;
    justify-content: space-between;
    align-items: center;
}
#logo-title {
    text-transform: uppercase;
    color: var(--main-text-color);
    font-size: 18px;
    font-weight: 500;
}
@media only screen and (max-width: 520px) {
    nav {
        flex-direction: column;
    }
    #logo-title {
        margin-bottom: 1rem;
    }
}