@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;800&display=swap);
@import url(https://cdn.jsdelivr.net/npm/@splidejs/splide@latest/dist/css/splide.min.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: var(--main-text-color);
  font-family: 'Montserrat', sans-serif;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading {
    font-size: 1rem;
    font-weight: 500;
}
.sub-heading {
    font-size: 1.5rem;
    font-weight: 500;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.details-container {
    background-color: #c4c4c480;
    width: 185px;
    justify-content: space-between;
    display: block;
}
.basic-details {
    display: flex;
    padding: 0.5rem 0.5rem 1rem 0.5rem;
}
.details-intro {
    display: flex;
    text-align: left;
    width: 70%;
}
.title {
    font-weight: 800;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.release-date {
    font-size: 15px;
    font-weight: 400;
}
.rating {
    display: flex;
    justify-content: center;
    align-items: center;
}
.popularity-container {
    position: relative;
    background-color: white;
    padding: 1rem 0;
    display: flex;
    justify-content: center;
}
.popularity-meter {
    position: absolute;
    top: -10px;
    width: 100%;
    background-color: black;
    width: 80%;
}
.meter {
    color: white;
}
.popularity-meter::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 5px;
    background-color: #919191;
    height: 100%;
}
.popularity-meter:hover::before {
    background-color: #919191;
    -webkit-animation: 0.3s scaleWidth ease-in-out forwards;
            animation: 0.3s scaleWidth ease-in-out forwards;
}
.picture-container {
    position: relative;
}
.picture-overlay {
    background-color: rgba(0, 0, 0, 0.8);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: 0.5s all ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.2rem;
}
p {
    color: white;
    font-size: 12px;
    height: 150px;
    overflow-y: scroll;
    text-overflow: ellipsis;
    text-align: left;
    line-height: 1.2rem;
    padding: 1rem;
}
p::-webkit-scrollbar {
    width: 5px;
}
p::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 5px;
}
p::-webkit-scrollbar-thumb {
    background: rgba(255, 0, 0, 0.767); 
    border-radius: 10px;
}
p::-webkit-scrollbar-thumb:hover {
    background: red; 
}
.picture-overlay:hover {
    opacity: 1;
}
.content-container:hover {
    cursor: pointer;
}
.poster:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@-webkit-keyframes scaleWidth {
    100% {
        width: 100%;
        content: 'Popularity Meter';
        color: white;
        text-overflow: ellipsis;
        overflow: hidden;
        font-size: 15px;
    }
}

@keyframes scaleWidth {
    100% {
        width: 100%;
        content: 'Popularity Meter';
        color: white;
        text-overflow: ellipsis;
        overflow: hidden;
        font-size: 15px;
    }
}
.splide__pagination {
    display: none;
}
nav {
    background-color: var(--main-theme-color);
    display: flex;
    padding: 1.5rem;
    justify-content: space-between;
    align-items: center;
}
#logo-title {
    text-transform: uppercase;
    color: var(--main-text-color);
    font-size: 18px;
    font-weight: 500;
}
@media only screen and (max-width: 520px) {
    nav {
        flex-direction: column;
    }
    #logo-title {
        margin-bottom: 1rem;
    }
}
input{
    border: 0;
    padding: 0.5rem;
    width: 15rem;
}
input:focus {
    outline: 0;
}
input::-webkit-input-placeholder {
    font-style: italic;
    font-size: 10px;
}
input:-ms-input-placeholder {
    font-style: italic;
    font-size: 10px;
}
input::placeholder {
    font-style: italic;
    font-size: 10px;
}

.search-box {
    display: flex;
}
.magnifying-glass-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    padding-right: 0.5rem;
}
.magnifying-glass {
    height: 15px;
}
.magnifying-glass-container:hover {
    cursor: pointer;
}

.slider {
    margin-top: 1rem;
}
.local-heading {
    text-align: left;
    padding: 0.5rem 1.5rem;
}

.local-sub-heading {
    margin-top: 1rem;
    text-align: left;
    padding-left: 1.5rem;
}
